/* Navigation */
/* ##################################################### */
/* ############## MOBILE MENU SLIDE TOGGLE ############# */
/* ##################################################### */
#menuslide { position: absolute; right: 2em; top: 2em; border: none; cursor: pointer; display: none; z-index: 100; width: 35px; height: 25px; transform: rotate(0deg); transition: .5s ease-in-out; }

#menuslide span { display: block; position: absolute; height: 5px; width: 100%; background: #e3000b; border-radius: 5px; opacity: 1; left: 0; transform: rotate(0deg); transition: .25s ease-in-out; }

#menuslide span:nth-child(1) { top: 0px; }

#menuslide span:nth-child(2), #menuslide span:nth-child(3) { top: 10px; }

#menuslide span:nth-child(4) { top: 20px; }

#menuslide.open span:nth-child(1) { top: 10px; width: 0%; left: 50%; }

#menuslide.open span:nth-child(2) { transform: rotate(45deg); }

#menuslide.open span:nth-child(3) { transform: rotate(-45deg); }

#menuslide.open span:nth-child(4) { top: 10px; width: 0%; left: 50%; }

/* ##################################################### */
/* ############## BACK TO TOP ########################## */
/* ##################################################### */
.back-to-top { background: #e3000b; color: #606060; position: fixed; bottom: 2em; right: 2em; padding: 1em; border: none; text-decoration: none; display: none; z-index: 100; opacity: .3; transition: opacity .5s; }

.back-to-top:hover { opacity: 1; }

.back-to-top span { display: block; color: #ffffff; }

/* ##################################################### */
/* ############## SONSTIGES ############################ */
/* ##################################################### */
.arrow { position: absolute; bottom: 1em; left: 50%; z-index: 10; margin-left: -1em; }

.arrow a { display: block; border: none; color: #ffffff; }

.arrow a span { padding: 0; font-size: 2.5em; }

.bounce { animation: bounce 3s infinite; }

@keyframes bounce { 0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
  40% { transform: translateY(-20px); }
  60% { transform: translateY(-10px); } }

/* allgemeines Layout */
/* ##################################################### */
/* ############## HEADER ############################### */
/* ##################################################### */
body { margin-left: 370px; text-align: center; background-color: #f7f0e3; }

/* ##################################################### */
/* ############## ASIDE ################################ */
/* ##################################################### */
aside { position: fixed; left: 0; top: 0px; bottom: 0px; z-index: 20; width: 370px; max-width: 65%; height: 100%; border-right: 1px solid #e0e0e0; display: flex; flex-direction: column; overflow-y: auto; background-color: #ffffff; transition: left 0.3s ease-in; }

aside.show { left: 0; }

aside .logo a { padding: 3em; display: block; }

aside .logo a img { width: 200px; }

aside .main ul li a, aside .main ul li strong, aside .footer ul li a, aside .footer ul li strong { font-family: "Fira Sans Condensed", sans-serif; font-weight: 400; display: block; padding: 0.7em 1em; border-bottom: 1px solid #e0e0e0; color: #838383; letter-spacing: .7px; }

aside .footer { margin-top: auto; }

aside .footer ul { display: flex; justify-content: center; }

aside .footer ul li a, aside .footer ul li strong { padding: 0 0.8em; letter-spacing: .7px; font-size: 0.8em; border: none; }

aside .partner { display: flex; justify-content: center; padding: 0 1em; border-top: 1px solid #e0e0e0; }

aside .partner a { display: block; width: 50%; padding: 1.5em 1em; }

aside .partner a img { max-width: 110px; }

/* ##################################################### */
/* ############## FOOTER ############################### */
/* ##################################################### */
footer { background-color: #ffffff; color: #bbbbbb; padding: 8em 2em; margin-top: 6em; border-top: 1px solid #e0e0e0; font-family: "Fira Sans Condensed", sans-serif; font-weight: 400; letter-spacing: .7px; }

footer .logo { max-width: 300px; margin: 0 auto 4em; }

footer .logo a { border: none; }

/* ##################################################### */
/* ############## ALLGEMEIN ############################ */
/* ##################################################### */
[class^="col-"], [class*=" col-"] { display: grid; grid-gap: 3em; }

.col-2 { grid-template-columns: repeat(auto-fill, minmax(450px, 1fr)); }

.col-3 { grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); }

.col-4 { grid-template-columns: repeat(auto-fill, minmax(240px, 1fr)); }

.col-5 { grid-template-columns: repeat(auto-fill, minmax(190px, 1fr)); }

.inner { max-width: 1300px; margin: 0 auto; padding: 3em; }

/* ##################################################### */
/* ############## TEXT/MEDIEN ########################## */
/* ##################################################### */
article { background-color: #ffffff; margin: 0 0 4em; }

article .infos { text-align: left; padding: 3em; }

article .infos h2 { padding: 0; color: #e3000b; }

article figure { float: right; margin: 2em 2em 2em 2em; max-width: 30%; }

article figure img { max-height: 400px; }

article:after { content: ""; display: table; clear: both; }

/* ##################################################### */
/* ############## MEDIEN ############################### */
/* ##################################################### */
figure { margin: 0; }

figure video { object-fit: cover; }

figure.full video { width: 100%; }

.image-slider { background-color: #fff; padding: 25px 20px; border-bottom: 1px solid #e0e0e0; border-top: 1px solid #e0e0e0; }

.home { display: flex; justify-content: center; position: relative; flex-wrap: wrap; height: 100vh; }

.home .bannercontent { width: 100%; padding: 2em 0; height: 50vh; display: flex; flex-direction: column; }

.home .bannercontent .logo { display: none; margin-bottom: 0; padding: 2em 2em 0; }

.home .bannercontent .logo a { border: none; text-decoration: none; }

.home .bannercontent .logo a img { margin: 2em 0; max-width: 200px; }

.banner { position: relative; z-index: -1; }

.banner.scrollfade { transition: opacity 1.5s; opacity: 1; }

.banner.scrollfade.scrolled { opacity: .1; }

.banner.nofade { opacity: .1; }

.banner.nofade img { display: block !important; }

.banner img.background { position: fixed; top: 0; right: 0; bottom: 0; width: 100%; height: 100%; z-index: -1; object-fit: cover; object-position: center left; display: none; height: 100vh; /* Fallback for browsers that do not support Custom Properties */ height: calc(var(--vh, 1vh) * 100); }

.galerie { list-style: none; }

.galerie ul { list-style: none; margin: 3em 0 0; padding: 0; }

.galerie ul li { border: 1em solid #ffffff; }

/* ##################################################### */
/* ############## FEATURE ############################## */
/* ##################################################### */
#feature { min-height: 100vh; display: flex; align-items: center; position: relative; }

.feature-wrap .feature { display: flex; flex-direction: column; background-color: #ffffff; }

.feature-wrap .feature img { object-fit: cover; }

.feature-wrap .feature h3 { padding: 0.7em; text-transform: none; display: flex; min-height: 120px; align-items: center; justify-content: center; }

.feature-wrap .feature p { padding: 0 1.35em; margin: 0 0 2em; }

.feature-wrap .feature .button { margin-top: auto; }

/* ##################################################### */
/* ############## RECHERCHE ############################ */
/* ##################################################### */
.tx-dlf-sortinfo { opacity: 0.8; margin-top: 2em; font-family: "Fira Sans Condensed", sans-serif; font-weight: 400; letter-spacing: 1px; }

.pagination { padding: 1em; margin: 1em 2em; background-color: rgba(247, 240, 227, 0.8); box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05); }

.pagination .tx-dlf-pagebrowser { display: flex; flex-wrap: wrap; justify-content: center; }

.pagination .tx-dlf-pagebrowser a, .pagination .tx-dlf-pagebrowser span { transition: none; display: block; padding: 1em .8em; margin: 0.3em; border: none; line-height: 1; min-width: 3em; font-family: "Fira Sans Condensed", sans-serif; font-weight: 400; background-color: #ffffff; }

.pagination .tx-dlf-pagebrowser a:hover { background-color: #f0f0f0; }

.pagination .tx-dlf-pagebrowser .active { background-color: #e3000b; color: #ffffff; font-weight: 600; }

.tx-dlf-search-facets { display: none; }

/* ##################################################### */
/* ############## SUCHERGEBNISSE ####################### */
/* ##################################################### */
.tx-dlf-listview { max-width: 900px; margin: 0 auto; font-family: "Fira Sans Condensed", sans-serif; font-weight: 400; letter-spacing: 1px; /* Dropdowns, noch klren ob notwendig */ }

.tx-dlf-listview h2 { padding: 1em 0; letter-spacing: 0; }

.tx-dlf-listview .tx-dlf-search-numHits, .tx-dlf-listview .tx-dlf-listview-counts { opacity: .7; margin: 0; line-height: 1.5; }

.tx-dlf-listview > form { display: none; }

.tx-dlf-listview ol { list-style: none; margin: 0; padding: 0; }

.tx-dlf-listview ol .tx-dlf-listview-list img { border: 1em solid #ffffff; }

.tx-dlf-listview ol dl dd.tx-dlf-metadata-title { display: block; font-size: 1.2em; line-height: 1.2; }

.tx-dlf-listview ol ol li { padding: 1em; margin: 1em 2em; background-color: rgba(247, 240, 227, 0.8); box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05); }

/* ##################################################### */
/* ######### SUCHERGEBNISSE ALTES MARKUP ############### */
/* ##################################################### */
.tx-dlf-abstracts { max-width: 900px; margin: 0 auto; list-style: none; padding: 0; }

.tx-dlf-abstracts > li { background-color: #ffffff; margin: 1em 0; position: relative; }

.tx-dlf-abstracts > li .value { color: #ffffff; display: block; top: 0; left: 0; padding: 0.5em; position: absolute; background-color: #e0e0e0; font-weight: 600; line-height: 1; margin: 0; font-size: 1.4em; }

.tx-dlf-morevolumes { font: 0/0 Arial, sans-serif; color: transparent; position: relative; cursor: pointer; transition: all 0.3s linear 0s; }

.tx-dlf-morevolumes:hover:after { background-color: #bbbbbb; }

.tx-dlf-morevolumes:after { content: "+"; position: absolute; right: 0; color: #ffffff; font-weight: 600; font-size: 30px; bottom: 0px; padding: 0.3em 0.5em; background-color: #e0e0e0; height: 50px; line-height: 1.1; }

.tx-dlf-morevolumes.open:after { content: "-"; padding: 0.3em 0.63em; }

.tx-dlf-hidevolumes { display: none; }

.tx-dlf-volume { background-color: #f0f0f0; margin: 0; padding: 0.5em 0; list-style: none; display: flex; flex-wrap: wrap; justify-content: center; }

.tx-dlf-volume li { background-color: #ffffff; margin: 0.5em; white-space: nowrap; overflow: hidden; max-width: 22%; text-overflow: ellipsis; }

.tx-dlf-volume li dd.tx-dlf-title { font-size: 0.9em; padding: 0; text-overflow: ellipsis; white-space: nowrap; overflow: hidden; }

dl { padding: 1em; }

dt.tx-dlf-title, dt.tx-dlf-type { display: none; }

dd.tx-dlf-title { font-size: 1.2em; display: block; padding: 0 1em 0.5em; }

dd, dt { margin: 0; padding: 0 0.2em; display: inline; color: #838383; font-size: 0.8em; }

.imgsearch figure { background-color: #ffffff; display: flex; justify-content: center; align-items: center; position: relative; padding: 1.5em; margin: 1.5em 0; }

.imgsearch figure figcaption { position: absolute; left: 0; top: 3em; display: inline-block; background-color: #e3000b; color: #ffffff; padding: 0.7em 1em; font-weight: 600; font-size: 1.1em; }

/* ##################################################### */
/* ############## COOKIE-BAR ########################### */
/* ##################################################### */
.cookie-message { position: fixed; width: 70%; max-width: 800px; bottom: 2em; left: 2em; right: 0; padding: 1em; z-index: 99; font-size: 0.8em; background-color: rgba(247, 240, 227, 0.8); text-align: left; }

.cookie-message .wrap { background: #ffffff; display: flex; padding: 1em; align-items: flex-end; }

.cookie-message .wrap a { display: block; background-color: #e3000b; padding: .5em 1em; margin: 0 .5em .5em 3em; color: #ffffff; white-space: nowrap; }

/* Responsive Layout*/
/* ##################################################### */
/* ############## BACK TO TOP ########################## */
/* ##################################################### */
img { max-width: 100%; height: auto; }

/* ##################################################### */
/* ############## MAX 1280px ########################### */
/* ##################################################### */
@media only screen and (max-width: 1280px) { body { margin-left: 300px; }
  aside { width: 300px; }
  .arrow { display: none; } }

/* ##################################################### */
/* ############## MAX 1024px ########################### */
/* ##################################################### */
@media only screen and (max-width: 1024px) { html { font-size: 95%; }
  body { margin-left: 0; }
  aside { left: -300px; }
  aside .footer ul { display: block; }
  #menuslide { display: flex; }
  .home { display: block; padding-top: 6em; }
  .home .bannercontent { background-color: rgba(247, 240, 227, 0.8); box-shadow: 0 0 30px 0 rgba(0, 0, 0, 0.05); margin: 0 2em 2em; padding: 0; display: block; height: auto; width: auto; }
  .home .bannercontent .logo { display: block; }
  .home .tx-dlf-search form > div { background: transparent; box-shadow: none; } }

/* ##################################################### */
/* ############## MAX 768px ############################ */
/* ##################################################### */
@media only screen and (max-width: 768px) { .col-2 { grid-template-columns: repeat(auto-fill, minmax(400px, 1fr)); }
  .col-3 { grid-template-columns: repeat(auto-fill, minmax(260px, 1fr)); }
  .col-4 { grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); }
  .col-5 { grid-template-columns: repeat(auto-fill, minmax(160px, 1fr)); }
  .inner { padding: 2em; }
  article .infos { padding: 2em; }
  .cookie-message { width: 90%; left: 5%; }
  .cookie-message .wrap { display: block; }
  .cookie-message .wrap a { text-align: center; margin: 1em 0 0; } }

/* ##################################################### */
/* ############## MAX 568px ############################ */
/* ##################################################### */
@media only screen and (max-width: 568px) { html { font-size: 85%; }
  .home .bannercontent { padding: 2em 0; }
  .tx-dlf-search form > div input#tx-dlf-search-query { min-width: auto; width: 70%; }
  .tx-dlf-search form > div button[type="submit"] { width: auto; }
  .col-2 { grid-template-columns: repeat(auto-fill, minmax(300px, 1fr)); }
  .col-3 { grid-template-columns: repeat(auto-fill, minmax(240px, 1fr)); }
  .col-4 { grid-template-columns: repeat(auto-fill, minmax(180px, 1fr)); }
  .col-5 { grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); }
  .feature-wrap .feature h3 { min-height: 0; padding: 1.5em .7em; }
  article figure { float: none; text-align: left; padding: 2em 2em 1em; margin: 0; max-width: 250px; } }

/* ##################################################### */
/* ############## MAX 320px ############################ */
/* ##################################################### */
@media only screen and (max-width: 320px) { html { font-size: 80%; }
  .tx-dlf-search form > div label { width: 100%; }
  .col-2 { grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); }
  .col-3 { grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); }
  .col-4 { grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); }
  .col-5 { grid-template-columns: repeat(auto-fill, minmax(150px, 1fr)); } }

/* Cross-Browser Styling */
/* ##################################################### */
/* ############## INTERNET EXPLORER 11 ################# */
/* ##################################################### */
.ie11 [class^="col-"], .ie11 [class*=" col-"] { display: flex; flex-wrap: wrap; justify-content: space-between; }

.ie11 [class^="col-"] > *, .ie11 [class*=" col-"] > * { margin-bottom: 2em; }

.ie11 .col-2 > * { width: 47%; }

.ie11 .col-3 > * { width: 30%; }

.ie11 .col-4 > * { width: 21%; }

.ie11 .col-5 > * { width: 17%; }

.ie11 .banner { overflow: hidden; position: static; }

.ie11 .banner img.background { height: auto !important; width: 1700px; max-width: 1700px; }

@media only screen and (max-width: 768px) { .ie11 .col-2 > * { width: 100%; }
  .ie11 .col-3 > * { width: 100%; }
  .ie11 .col-4 > * { width: 100%; }
  .ie11 .col-5 > * { width: 100%; } }

